import React, { Component } from 'react';
import { Row, Col, Menu, Select, Button, Layout, Slider, Icon } from 'antd';
import Dog from "./components/Dog";
import Dogs from "./dogs";
import './App.css';

const DogsNames = Object.values(Dogs);
const Option = Select.Option;
const des = `Some traits for the dog n1. There are many variations of passages of Lorem Ipsum available, 
but the majority have suffered alteration in some form, by injected humour, 
or randomised words which don't look even slightly believable.`;
class App extends Component {

  state = {
    dog1: 'papillon', dog2: 'toy terrier',
    show: false,
    transitionslider: 4,
    transitioncounter:1,
    sliderstarted:false
  };

  handleDogChange = (name) => (value) => {
    this.setState({ [name]: value });
  };

  handleDogSliderChange = (val) => {
    this.setState({ transitionslider: val });
  };

  showResult = () => {
    this.setState({ show: true })
    this.playDogTransition()
  };

  tryAgain = () => {
    this.setState({ dog1: '', dog2: '', show: false })
  };

  playDogTransition = () => {
    if(this.state.sliderstarted){
      console.log('Stopping slider')
      clearInterval(this.intervalID);
      this.setState({sliderstarted:false})        
    }
    else{
      this.setState({sliderstarted:true})        
      console.log('Starting')
      this.intervalID = setInterval(() => {
          this.slidertick()},
        250
      );
    }
   
  }

  slidertick() {
    const tickval =  this.state.transitionslider
    if(tickval===0){
      this.setState({ transitioncounter: +1 })
    }
    if(tickval===7){
      this.setState({ transitioncounter: -1 })
    }
    this.setState({ transitionslider: tickval + this.state.transitioncounter })

  
  }

  componentWillUnmount() {
    clearInterval(this.intervalID);
  }

  render() {
    const { dog1, dog2, show, transitionslider } = this.state;

    return (
      <div className="App">
        <Layout.Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
          <div className="logo">need4breed</div>
          <Menu
            className="top-nav"
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={['2']}
            style={{ lineHeight: '64px' }}
          >
            <Menu.Item key="1">Dog breeder</Menu.Item>
            <Menu.Item key="2">Dog art</Menu.Item>
            <Menu.Item key="3">Dog quiz</Menu.Item>
          </Menu>
        </Layout.Header>
        <div className="dog-breeder">
          <div className="white-bg">
            <Row>
              <Col span={24}>
                <h4 className="heading">Dog breeder</h4>
              </Col>
              <Col xs={24} sm={{ offset: 6, span: 12 }}>
                <div className="how-to-use">
                  How to use this website. There are many variations of passages of Lorem Ipsum available,
                  but the majority have suffered alteration in some form, by injected humour,
                  or randomised words which don't look even slightly believable.
                </div>
              </Col>
            </Row>
            <Row gutter={20}>
              <Dog
                title="dog 1"
                colProps={{ xs: 24, sm: 12, lg: { offset: 4, span: 8 } }}
                image={`https://s3.eu-central-1.amazonaws.com/crossbreeddog-production/${dog1.replace(/ /g, '%20')}_${dog2.replace(/ /g, '%20')}-crossbreed_0.jpg`}
                description={des}>
                <Select
                  value={dog1}
                  onChange={this.handleDogChange('dog1')}
                  style={{ width: '100%' }}>
                  {
                    DogsNames.map((name, i) => (
                      <Option value={name} key={name}>{name}</Option>
                    ))
                  }
                </Select>
              </Dog>
              <Dog
                title="dog 2"
                colProps={{ xs: 24, sm: 12, lg: 8 }}
                image={`https://s3.eu-central-1.amazonaws.com/crossbreeddog-production/${dog1.replace(/ /g, '%20')}_${dog2.replace(/ /g, '%20')}-crossbreed_7.jpg`}
                description={des}>
                <Select
                  value={dog2}
                  onChange={this.handleDogChange('dog2')}
                  style={{ width: '100%' }}>
                  {
                    DogsNames.map((name, i) => (
                      <Option value={name} key={name}>{name}</Option>
                    ))
                  }
                </Select>
              </Dog>
            </Row>
            <Row>
              <Col className="m-b-50">
                <Button onClick={this.showResult} disabled={!(dog1 && dog2)}>show me the crossbreed!</Button>
              </Col>
            </Row>
          </div>
          {
            show &&
            <div className="gray-bg">
              <Row>
                <Dog
                  title="here is your crossbreed"
                  image={`https://s3.eu-central-1.amazonaws.com/crossbreeddog-production/${dog1.replace(/ /g, '%20')}_${dog2.replace(/ /g, '%20')}-crossbreed_${transitionslider}.jpg`}
                  description={des} />
              </Row>
              <Row>
                <Col xs={2} sm={4} md={6} lg={8} xl={10}>

                </Col>
                <Col xs={20} sm={16} md={12} lg={8} xl={4}>
               {
                 this.state.sliderstarted ? <Icon type="pause" onClick={this.playDogTransition.bind(this)} /> : <Icon type="caret-right" onClick={this.playDogTransition.bind(this)} />

               } 
                  <Slider defaultValue={4} min={0} max={7} value={this.state.transitionslider} onChange={this.handleDogSliderChange} />
                </Col>
                <Col xs={2} sm={4} md={6} lg={8} xl={10}>

                </Col>

              </Row>
              <Row>
                <Col className="m-b-50">

                  <Button onClick={this.tryAgain}>try again!</Button>
                </Col>
              </Row>
            </div>
          }
          <Row className="p-t-15">
            <Col xs={24} sm={24} md={5} lg={5} className="p-l-70">
              <h5>Footnote 1</h5>
              <h5>Footnote 2</h5>
            </Col>
            <Col xs={24} sm={24} md={8} lg={8}>
              <h5>Footnote 4</h5>
              <h5>Made with ML and dog love</h5>
            </Col>
          </Row>
        </div>
      </div>
    );
  }

}

export default App;
